body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.Layout {
    margin: 10px;
}

.loading {
    position: absolute;
    background: rgba(191, 191, 191, 0.49);
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;

    display: flex;
    justify-content: center;
    align-items: center;
}

.titles {
    margin-top: 50px;
    display: flex;
}

footer {
    margin-top: 50px;
}

.description {
    width: 40%;
    border: black solid 1px;
}

.assignee {
    width: 20%;
    border: black solid 1px;
}

.assign {
    width: 40%;
    display: flex;
    justify-content: space-around;
    border: black solid 1px;
}

.row {
    display: flex;
}

.container{
    margin: 100px auto;
    max-width: 700px;
}
